.main-footer {
  padding: 50px 0;
  background-color: @header-background;

  ul {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;

    @media (max-width: 767px) {
      flex-direction: column;
    }
  }

  li {
    transition: all 0.6s ease-in-out;

    &:hover {
      transform: scale(1.1);
    }
  }

  a {
    color: #ffffff;
    font-size: 20px;
    transition: all 0.6s ease-in-out;

    span {
      margin-left: 20px;
    }

    &:hover {
      opacity: 0.6;
    }
  }
}
