.header-nav {
  padding: 40px 0;

  ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: row;
  }

  li {
    &:not(:last-child) {
      margin-right: 20px;
    }
  }

  a {
    border: 2px solid @header-background;
    border-radius: 10px;
    padding: 10px;
    background-color: #ffffff;
    color: @header-background;
    transition: all 0.6s ease-in-out;

    &:hover {
      background-color: @header-background;
      color: #ffffff;
    }
  }
}
