.credo {
  background-color: @header-background;

  h2 {
    color: #ffffff;
    margin-bottom: 30px;
  }

  p {
    padding: 0;
    margin: 0;
    font-style: italic;
    font-size: 36px;
  }

  &__text {
    padding: 30px;
    border-radius: 20px;
    border: 2px solid #ffffff;
    background-color: #ffffff;
  }
}
