.main-header {
  padding: 90px 0;
  background-color: @header-background;

  p {
    color: #ffffff;
    margin: 0;
    margin-bottom: 10px;
    font-size: 36px;
    font-weight: 700;
    max-width: 690px;
  }

  span {
    color: #ffffff;
    font-size: 45px;
    font-weight: 700;
  }

  &__math {
    margin-right: 30px;
  }
}

.main-header__section {
  display: flex;
  flex-direction: row;
  align-items: center;
}
